<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue">
			<block slot="content">资讯</block>
		</cu-custom>

		<view class="cu-card dynamic" :class="isCard?'no-card':''" v-for="(item,i) in list" :key="i">
			<view class="cu-item shadow">
				<view class="cu-list menu-avatar">
					<view class="cu-item">
						<view class="cu-avatar round lg"
							style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg);">
						</view>
						<view class="content flex-sub">
							<view>管理员</view>
							<view class="text-gray text-sm flex justify-between">
								{{item.datetime}}
							</view>
						</view>
					</view>
				</view>
				<view class="text-content">
					{{item.title}}
				</view>
				<view class="grid flex-sub padding-lr" :class="isCard?'col-3 grid-square':'col-1'">
					<view class="bg-img" :class="isCard?'':'only-img'"
						:style="{backgroundImage: 'url('+item.trans_pic +')'}">
					</view>
				</view>
				<view class="text-gray text-sm text-right padding">
					<view>

					</view>
					<text v-if="item.isdianzan" class="cuIcon-favorfill margin-lr-xs" style="color: gray;"
						@tap="dianzanfun(item,'1')">收藏</text>
					<text v-else class="cuIcon-favorfill margin-lr-xs" style="color: red;"
						@tap="dianzanfun(item,'2')">取消</text>
					<text class="cuIcon-commentfill margin-lr-xs" @tap="seeeve(item)">查看评论</text>
				</view>

			</view>
		</view>

	</view>
</template>

<script>
	import configData from '@/config/index.js'
	const urllocal = configData.httpUrl
	export default {
		created() {

		},
		onShow() {
			this.userinfoObj = uni.getStorageSync('userinfo')
			this.init()
		},
		data() {
			return {
				userinfoObj: {},
				isCard: false,
				list: []
			}
		},
		methods: {
			async init() {
				let that = this
				let msgobj = {
					userid: this.userinfoObj.id
				}
				let resext = await new this.Request(this.Urls.m().queryannouncementuser, msgobj)
					.modepost()

				that.list = resext.map((val, i) => {
					val.trans_pic = urllocal + val.trans_pic.split('^^^')[0]
					let userid = val.userid
					let flag = true
					if (userid) {
						flag = false
					} else {
						flag = true
					}
					val.isdianzan = flag
					return val
				})
				console.log(resext,that.list)
				
			},
			getnowdate() {
				var nowDate = new Date();
				var year = nowDate.getFullYear();
				var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1) : nowDate.getMonth() + 1;
				var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
				var dateStr = year + "-" + month + "-" + day;
				return dateStr;
			},
			getnowtime() {
				let date = new Date()
				let hours = date.getHours()
				let minutes = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
				let seconds = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds()
				let time = hours + ':' + minutes + ':' + seconds
				return time

			},
			//收藏
			async dianzanfun(val, valbj) {
				let that = this
				let datetime = this.getnowdate() + " " + this.getnowtime()
				//收藏
				if (valbj == '1') {
					uni.showLoading({
						title: '加载中'
					});
					let msgobj = {
						userid: this.userinfoObj.id,
						shiwuid: val.id,
						shiwuname: val.title,
						datetime: datetime,
						typetext: '收藏',
					}
					let resext = await new this.Request(this.Urls.m().insertcollection, msgobj)
						.modepost()
					if (resext == 'success') {
						uni.showToast({
							title: '收藏成功',
							icon: 'none',
							duration: 2000
						});
						that.init()
					}
				} else {
					uni.showLoading({
						title: '加载中'
					});
					//删除收藏
					let msgobj = {
						userid: this.userinfoObj.id,
						shiwuid: val.id,
						typetext: '收藏',
					}
					let resext = await new this.Request(this.Urls.m().removecollection, msgobj)
						.modepost()
					if (resext == 'success') {
						uni.showToast({
							title: '取消收藏',
							icon: 'none',
							duration: 2000
						});
						that.init()
					}
				}
				//
			},
			onPullDownRefresh() {
				this.init()
				uni.stopPullDownRefresh();
			},
			dianzan(val) {

			},
			seeeve(val) {
				console.log(val)
				uni.navigateTo({
					url: `./comment?id=${val.id}&title=${val.title}&detail=${val.detail}`
				});
			}
		}
	}
</script>

<style>
	.mainsub {
		padding-left: 5%;
		padding-right: 5%;
		background-color: #fff;
	}
</style>